body {
    margin: 0px;
}

#root {
    position: absolute;
    width: 100%;
}

#App {
    display: grid;
    grid-template-columns: 40% 2fr;
    grid-template-rows: 94vh;
    margin-top: 5vh;
}

#previewer {
    margin-left: 30px;
    overflow: auto;
}

#TextArea {
    display: grid;
    grid-template-columns: 50px 1fr;
}

#TextArea > div {
    border: 1px solid green;
}

#TextArea-writter {
    white-space: pre;
    color: #82b5d0;
    background-color: #1e1e1e;
    padding-left: 14px;
}

textarea {
    padding-top: 7px;
    margin: 0px;
    resize: none;
    font-size: 14px;
    border: none;
}

.lines-number {
    text-align: right;
    color: #82b5d0;
    border-right: 5px solid #0c7d9d;
    caret-color: #1e1e1e;
    background-color: #1e1e1e;
    white-space: pre-wrap;
}

#title-bar {
    position: fixed;
    width: 100%;
    height: 5vh;
    margin: 0px;
    color: #0c7d9d;
    background-color: #252526;
    text-align: center;
    font-style: italic;
    border-bottom: 3px solid #0c7d9d;
}